@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
  border-bottom: 2px solid #ff9800;
  margin-bottom: 20px;
}
.footer{
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  padding: 10px;
  margin-top: 20px;
  font-size: 15px;
}
.home {
  max-width: 800px;
  margin: 0 auto;
}

.navbar .title {
  font-size: 30px;
  color: #ff9800;
}

.nav-lists{
  margin-left: auto;
}
.nav-lists a{
  padding: 0 10px;
}

.fact-preview {
  margin: 10px;
  padding: 10px;
  border-bottom: 1px solid #fafafa;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}

.fact-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
  background-color: #ffcc80;
  border-bottom: 1px solid #ffcc80;
}

.fact-preview p {
  font-size: 20px;
}
.add {
  max-width: 800px;
  margin: 0 auto;
}
